.nav{width:100%;height:auto;position:absolute;top:0;transition:all 600ms cubic-bezier(0.39, 0.58, 0.57, 1);background:#602a9a;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.nav__r{display:none}.nav__logo{display:flex;flex-direction:row;align-items:center}.nav__logo .burger{display:none;z-index:99}.nav__logo #logo img{width:5rem;padding:2rem}.nav__menu{display:flex;flex-direction:row;list-style:none}.nav__menu a{text-decoration:none;color:#fff;padding:0 2rem}.nav__social{display:block}.nav__social a img{width:2.5rem;padding:0 1rem;padding-right:2rem}@media(max-width: 1100px){.nav{background:transparent}.nav__logo #logo{display:none}.nav__logo .burger{display:block}.nav__r.show img{width:5rem;padding:0rem}.nav__menu{opacity:0;transform:translateY(-100%);transition:all 500ms ease-out}.nav__menu.show{opacity:1;transform:translateY(0);display:flex;flex-direction:column;align-items:center;justify-content:center;position:relative;width:100vw;height:100vh;background:#602a9a}.nav__menu.show a{font-size:1.5rem;display:block;padding:1.5rem}.nav__social{display:none}}.burger{text-decoration:none;border-style:none;background:transparent;width:60px;height:45px;position:absolute;top:20px;left:20px;transform:rotate(0deg);transition:.5s ease-in-out;cursor:pointer}.burger span{display:block;position:absolute;height:9px;width:100%;background:#fff;border-radius:9px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}.burger span:nth-child(1){top:0px}.burger span:nth-child(2),.burger span:nth-child(3){top:18px}.burger span:nth-child(4){top:36px}.burger.open span:nth-child(1){top:18px;width:0%;left:50%}.burger.open span:nth-child(2){transform:rotate(45deg)}.burger.open span:nth-child(3){transform:rotate(-45deg)}.burger.open span:nth-child(4){top:18px;width:0%;left:50%}
.Bio{display:grid;grid-template-areas:"foto title" "foto text" "foto button" "logos logos";grid-template-columns:20% 80%;margin:0 auto;margin-top:100px;max-width:1140px;height:auto}.Bio__image{margin:0 auto;grid-area:foto}.Bio__title{display:flex;align-items:flex-end;justify-content:center;grid-area:title;color:#313d28;padding-bottom:3rem}.Bio__text{grid-area:text;text-align:center;padding:0 13rem;font-size:1.2rem;text-justify:distribute;padding-bottom:2rem}.Bio__button{display:flex;justify-content:center;align-items:center;text-decoration:none;border-style:none;margin:0 auto;grid-area:button;font-size:1rem;font-weight:600;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:3rem;padding:0 2rem;color:#fff;background-color:#829c6f;border-radius:5rem;transition:all 300ms ease-in-out}.Bio__button:hover{cursor:pointer;background:#313d28}.Bio__logos{margin-top:3rem;display:flex;justify-content:center;grid-area:logos}.Bio__logos img{height:2.2rem;padding:2rem 1rem}@media(max-width: 768px){.Bio{display:grid;grid-template-areas:"foto" "title" "text" "button" "logos";grid-template-columns:100%}.Bio__image{max-width:100%}.Bio__title{margin-top:2rem;padding:1rem;font-size:2rem}.Bio__text{padding:1rem}.Bio__button{margin-top:1rem}.Bio__logos{display:grid;grid-template-columns:repeat(auto-fit, minmax(150px, 1fr));margin-top:2rem}.Bio__logos img{padding:.9rem;margin:0 auto}}

